/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import boosterConfig from 'config/constants/booster'
import fetchFarms from './fetchBooster'
import {
  fetchFarmUserEarnings,
  fetchFarmUserAllowances,
  fetchBoosterUserEarnings,
  fetchFarmUserTokenBalances,
  fetchFarmUserStakedBalances,
} from './fetchBoosterUser'
import { BoosterState, Farm } from '../types'

const initialState: BoosterState = { data: [...boosterConfig] }

export const farmsSlice = createSlice({
  name: 'Booster',
  initialState,
  reducers: {
    setBoosterPublicData: (state, action) => {
      const liveFarmsData: Farm[] = action.payload
      state.data = state.data.map((farm) => {
        const liveFarmData = liveFarmsData.find((f) => f.pid === farm.pid)
        return { ...farm, ...liveFarmData }
      })
    },
    setBoosterUserData: (state, action) => {
      const { arrayOfUserDataObjects } = action.payload
      arrayOfUserDataObjects.forEach((userDataEl) => {
        const { index } = userDataEl
        state.data[index] = { ...state.data[index], userData: userDataEl }
      })
    },
  },
})

// Actions
export const { setBoosterPublicData, setBoosterUserData } = farmsSlice.actions

// Thunks
export const fetchBoosterPublicDataAsync = () => async (dispatch) => {
  const farms = await fetchFarms()
  dispatch(setBoosterPublicData(farms))
}
export const fetchBoosterUserDataAsync = (account) => async (dispatch) => {

  
  const userFarmAllowances = await fetchFarmUserAllowances(account)
  const userFarmTokenBalances = await fetchFarmUserTokenBalances(account)
  const userStakedBalances = await fetchFarmUserStakedBalances(account)
  const userFarmEarnings = await fetchFarmUserEarnings(account)
  const userMultiplier = await fetchBoosterUserEarnings(account)
  const arrayOfUserDataObjects = userFarmAllowances.map((farmAllowance, index) => {
  
    console.log("userFarmEarnings",userFarmEarnings[index])
  
    return {
      index,
      allowance: userFarmAllowances[index],
      tokenBalance: userFarmTokenBalances[index],
      stakedBalance: userStakedBalances[index],
      earnings: userFarmEarnings[index].totalReward,
      bonusEarning: userFarmEarnings[index].bonusReward,
      normalEarning: userFarmEarnings[index].pending,
      bonusMultiplier:userMultiplier[index]
    }
  })

  dispatch(setBoosterUserData({ arrayOfUserDataObjects }))
}

export default farmsSlice.reducer
